import React, { useState, useCallback } from 'react';
import T from 'prop-types';
import styled from 'styled-components';
import { _ } from '@splunk/ui-utils/i18n';
import WaitSpinner from '@splunk/react-ui/WaitSpinner';
import { pick, variables } from '@splunk/themes';

const Container = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    padding: 5px;
    width: 515px;
    min-height: 200px;
    background-color: ${variables.backgroundColorDialog};
`;

const GalleryList = styled.div`
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    overflow: auto;
    padding: 5px 0px;
    width: 100%;
    height: 200px;
    border-bottom: ${pick({
        prisma: '1px solid #43454b',
        enterprise: '1px solid #c3cbd4',
    })};
`;

const spinnerStyle = { margin: 'auto' };

const Gallery = ({
    isLoading,
    search,
    message,
    upload,
    items,
    Item,
    onItemRemove,
    onItemClick,
    testId,
}) => {
    const hasItems = Array.isArray(items) && items.length > 0;
    const [selectedItem, setSelectedItem] = useState(null);

    const handleItemEnter = useCallback((item) => setSelectedItem(item.url), [
        setSelectedItem,
    ]);
    const handleItemLeave = useCallback(() => setSelectedItem(null), [
        setSelectedItem,
    ]);

    return (
        <Container data-test={testId} data-test-loading={`${isLoading}`}>
            {search}
            {message}
            {isLoading && (
                <WaitSpinner
                    size="medium"
                    screenReaderText={_('loading')}
                    style={spinnerStyle}
                />
            )}
            {!isLoading && hasItems && (
                <GalleryList data-test="gallery-list">
                    {items.map((i) => (
                        <Item
                            key={i.url}
                            item={i}
                            selected={selectedItem === i.url}
                            onMouseEnter={handleItemEnter}
                            onMouseLeave={handleItemLeave}
                            onRemove={onItemRemove}
                            onClick={onItemClick}
                        />
                    ))}
                </GalleryList>
            )}
            {!isLoading && upload}
        </Container>
    );
};

Gallery.propTypes = {
    testId: T.string,
    isLoading: T.bool,
    message: T.element,
    items: T.arrayOf(T.object),
    Item: T.elementType.isRequired,
    search: T.element,
    upload: T.element,
    onItemClick: T.func,
    onItemRemove: T.func,
};

Gallery.defaultProps = {
    testId: 'gallery',
    isLoading: false,
    search: null,
    message: null,
    upload: null,
    onItemClick: () => {},
    onItemRemove: () => {},
};

export default Gallery;
