import React from 'react';
import T from 'prop-types';
import styled from 'styled-components';
import Close from '@splunk/react-icons/Close';
import Message from './Message';

const MsgContainer = styled.div`
    display: inline-block;
    width: 95%;
    height: 200px;
    position: relative;
`;

const MsgClose = styled.div`
    float: right;
`;

const GalleryMessage = ({ error, warning, onClose }) => {
    if (!error && !warning) {
        return null;
    }

    const type = error ? 'error' : 'warning';
    const msg = error || warning;

    return (
        <MsgContainer>
            <Message level={type} message={msg} />
            {onClose && (
                <MsgClose onClick={onClose}>
                    <Close size={0.5} />
                </MsgClose>
            )}
        </MsgContainer>
    );
};

GalleryMessage.propTypes = {
    error: T.string,
    warning: T.string,
    onClose: T.func,
};

export default GalleryMessage;
