import React, { Component } from 'react';
import T from 'prop-types';
import sortBy from 'lodash/sortBy';
import get from 'lodash/get';

import { _ } from '@splunk/ui-utils/i18n';
import DashboardContext from '@splunk/dashboard-context';

import Gallery from './Gallery';
import GallerySearch from './GallerySearch';
import GalleryMessage from './GalleryMessage';
import GalleryUpload from './GalleryUpload';
import IconGalleryItem from './IconGalleryItem';

class IconGallery extends Component {
    static contextType = DashboardContext;

    static propTypes = {
        providerType: T.string,
        onClick: T.func,
        enableSearch: T.bool,
    };

    static defaultProps = {
        enableSearch: true,
        onClick: () => {},
    };

    constructor(props) {
        super(props);
        this.state = {
            icons: null,
            providerType: null,
            loading: true,
            searching: false,
            error: null,
            warning: null,
        };
    }

    componentDidMount() {
        this.fetchIcons();
    }

    getWarning = (icons, msg) => {
        if (icons && icons.length) {
            return null;
        }

        return msg;
    };

    getIconRegistry() {
        const iconRegistry = get(this.context, 'iconRegistry');
        if (iconRegistry == null) {
            throw new Error(_('No icon registry found'));
        }
        return iconRegistry;
    }

    handleIconUpload = (files) => {
        const isUploadEnabled = get(this.context, [
            'featureFlags',
            'enableIconUploads',
        ]);
        if (!isUploadEnabled) {
            return;
        }

        const { providerType } = this.state;
        this.setState({ loading: true });
        files.forEach((file) => {
            const fileReader = new FileReader();
            fileReader.onload = async () => {
                const fileName = file.name;
                try {
                    const iconURL = await this.getIconRegistry().upload(
                        fileReader.result,
                        { name: fileName },
                        providerType
                    );
                    const icons = [
                        ...this.state.icons,
                        {
                            url: iconURL,
                            name: fileName,
                        },
                    ];
                    this.setState({
                        icons: sortBy(icons, ['name']),
                        warning: null,
                        error: null,
                        loading: false,
                    });
                } catch (err) {
                    this.setState({
                        error: err.message,
                        loading: false,
                    });
                }
            };

            fileReader.readAsDataURL(file);
        });
    };

    /**
     * Handle searching icons from all providers
     */
    handleSearch = async (search) => {
        this.setState({ searching: true });
        try {
            const ids = await this.getIconsFromAllProviders({
                meta: ['name'],
                search: search.trim(),
            });
            const icons = ids.map((idObj) => ({
                url: `${idObj.providerType}://${idObj.id}`,
                name: idObj.metaData.name.split('.')[0],
            }));

            this.setState({
                icons: sortBy(icons, ['name']),
                error: null,
                warning: this.getWarning(icons, _('No icons found.')),
                searching: false,
            });
        } catch (err) {
            this.setState({
                error: err.message,
                searching: false,
            });
        }
    };

    handleDelete = async (deletedIcon) => {
        const isRemoveEnabled = get(this.context, [
            'featureFlags',
            'enableIconDelete',
        ]);
        if (!isRemoveEnabled) {
            return;
        }

        this.setState({ loading: true });
        try {
            await this.getIconRegistry().deleteByURL(deletedIcon.url);
            this.setState((prevState) => {
                const icons = prevState.icons.filter(
                    (icon) => icon.url !== deletedIcon.url
                );
                return {
                    loading: false,
                    icons,
                    warning: this.getWarning(
                        icons,
                        _('No Icons. Please upload icons.')
                    ),
                };
            });
        } catch (err) {
            this.setState({
                error: err.message,
                loading: false,
            });
        }
    };

    /**
     * Returns a concatenated array of icons from all providers
     * @param {Object} options
     * @returns {Promise<Array>}
     */
    getIconsFromAllProviders = async (options) => {
        const iconRegistry = this.getIconRegistry();
        const providers = iconRegistry.listProviders();
        const iconLists = await Promise.all(
            providers.map(async (provider) => {
                const { type } = provider;
                const ids = await iconRegistry.listIds(type, {
                    ...options,
                    meta: ['name'],
                });
                return ids.map((id) => ({ ...id, providerType: type }));
            })
        );

        return iconLists.reduce(
            (iconList, currentList) => iconList.concat(currentList),
            []
        );
    };

    fetchIcons = async () => {
        try {
            let { providerType } = this.props;
            if (!providerType) {
                const provider = this.getIconRegistry()
                    .listProviders()
                    .find((type) => type.isDefault);

                if (!provider) {
                    throw new Error('No icon providers found');
                }

                providerType = provider.type;
            }

            const ids = await this.getIconsFromAllProviders();
            const icons = ids.map((idObj) => ({
                url: `${idObj.providerType}://${idObj.id}`,
                name: idObj.metaData.name.split('.')[0],
            }));

            this.setState({
                icons: sortBy(icons, ['name']),
                providerType,
                error: null,
                loading: false,
                warning: this.getWarning(
                    icons,
                    _('No icons. Please upload icons.')
                ),
            });
        } catch (err) {
            this.setState({
                error: err.message,
                loading: false,
            });
        }
    };

    handleItemClick = (url) => this.props.onClick(url);

    renderSearch() {
        return (
            <GallerySearch
                placeholder={_('Search Icons')}
                onSearch={this.handleSearch}
            />
        );
    }

    renderMessage() {
        return (
            <GalleryMessage
                error={this.state.error}
                warning={this.state.warning}
            />
        );
    }

    render() {
        const { enableSearch } = this.props;
        const { icons, loading, error, searching } = this.state;
        const {
            enableIconUploads: isUploadEnabled,
            enableIconDelete: isRemoveEnabled,
            enableGallerySharingWarning,
        } = get(this.context, 'featureFlags', {});

        return (
            <Gallery
                testId="icon-gallery"
                isLoading={loading || searching}
                // Do not display search input if it is explicitly disabled, there is an error,
                // or icons have not been initialized (null)
                search={
                    enableSearch && !error && icons ? this.renderSearch() : null
                }
                message={this.renderMessage()}
                items={icons}
                Item={IconGalleryItem}
                upload={
                    error || !isUploadEnabled ? null : (
                        <GalleryUpload
                            fileTypes=".svg"
                            onUpload={this.handleIconUpload}
                            enableGallerySharingWarning={
                                enableGallerySharingWarning
                            }
                        />
                    )
                }
                onItemClick={this.handleItemClick}
                onItemRemove={isRemoveEnabled ? this.handleDelete : null}
            />
        );
    }
}

export default IconGallery;
